import React from 'react'
import styled from 'styled-components'
import { IOptionText } from '../../types/index';
import { Colors, Metrics, Fonts, Styles } from '../../Themes'

const TextGroup = styled(Styles.OptionTextWrapper())`
`
const Card = styled(Styles.OptionTextCard())`
`
const ImgArea = styled.div`
    border-radius: 100%;
    overflow: hidden;
    margin: 40px 32px;
    width: 50px;
    height: 50px;
    padding: 0;
    @media (max-width: ${Metrics.tablet}){
        margin: 16px;
        width: 30px;
        height: 30px;
        padding: inherit
    }
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active){
        border-radius: 50px;
        width: 50px;
        height: 50px;
    }
    @media (max-width: ${Metrics.tablet}) and (-ms-high-contrast: none), (-ms-high-contrast: active){
        margin: 16px;
        width: 30px;
        height: 30px;
    }
`

const Img = styled.img`
    width: 150%;
    height: auto;
`
const TextArea = styled(Styles.OptionTextTextArea())`
    width: auto;
    overflow: inherit;
    .Text{
        -ms-overflow-style: none;
        color: ${Colors.TitleBlack};
        font-size: 24px;
        letter-spacing: 1px;
        line-height: 32px;
        margin: 0;
        text-align: left;
        margin-left: 15px;
    }
    @media (max-width: ${Metrics.tablet}){
        .Text{
            font-size: 16px;
            letter-spacing: 1px;
            line-height: 24px;
            margin-left: inherit;
            width: 100%
        }
    }
`
const Text = styled(Fonts.optionText())`
`

const OptionText = (props: IOptionText) => {
    
  return (
    <TextGroup>
        <Card>
            <ImgArea>
                <Img src={props?.portrait} />
            </ImgArea>
            <TextArea>
                <Text>{props?.text}</Text> 
            </TextArea>
        </Card>
    </TextGroup>
  )
}

export default OptionText
